import React, {useState} from "react"
import {FaArrowUp, FaBars} from "react-icons/fa"
import {FiSettings, FiLogOut} from "react-icons/fi"
import {BiRightArrow} from "react-icons/bi"
import { animateScroll as scroll} from "react-scroll"

function DropdownMenu(){

    function DropdownItem(props){
        return(
            <a href="#" className="menuitem">
                <span className="iconbutton">{props.leftIcon}</span>
                {props.children}
                <span className="iconbutton">{props.rightIcon}</span>
                </a>
        )
    }

    return (
        <div className="dropdown">
            <DropdownItem leftIcon={<FiSettings/>} rightIcon={<BiRightArrow/>}>Settings</DropdownItem>
            <DropdownItem  leftIcon={<FiLogOut/>} rightIcon={<BiRightArrow/>}>Log out</DropdownItem>
        </div>
    )
}

function NavItem(props){

const [isOpen, setIsOpen] = useState(false)

    return (
        <li className="navitem">
            <p className="iconbutton" onClick={()=>setIsOpen(!isOpen)}>
                {props.icon}
            </p>
           {isOpen ? <DropdownMenu/>:<div></div>}
        </li>
    )
}

function NavToTop(props){
    
        return (
            <li className="navitem">
                <p className="iconbutton" onClick={props.event}>
                    {props.icon}
                </p>
              
            </li>
        )
    }

function Navbar (props) {

    return (
       <nav className="nav">
           <ul className="navnav">
                {props.children}
           </ul>
       </nav>
    )
    
}

function Navigation (){

    const toUp = () => {
        scroll.scrollToTop()
    }


    return(
       <Navbar>         
            <NavItem icon={<FaBars/>}/>  
            <NavToTop icon={<FaArrowUp/>} event={toUp}/> 
       </Navbar> 
    )
}

//export default NavItem
export default Navigation