import React, { useState, useEffect } from "react";
import postService from "./../services/julkaisut";
import "./../index.css";
import { FaHeart } from "react-icons/fa";

const Julkaisu = ({ posts, likePost }) => {
  return (
    <div className="julkaisu">
      <div className="content">
        <h1 className="user">{posts.user.username}</h1>
        <div className="contenttext">
          <p>{posts.content} </p>
        </div>
        <div className="buttons">
          <FaHeart className="likeButton" onClick={likePost} />
          {posts.likes} likes
          <button className="viewButton">View</button>
        </div>
      </div>
    </div>
  );
};

const AllJulkaisut = ({ posts, likePost, removeJulkaisu }) => {
  return (
    <div className="julkaisut">
      {posts.map((a) => (
        <Julkaisu
          likePost={() => likePost(a.id)}
          removeJulkaisu={() => removeJulkaisu(a.id)}
          key={a.id}
          posts={a}
        ></Julkaisu>
      ))}
    </div>
  );
};

const SingleJulkaisu = (props) => {
  return (
    <div>
      <h1>Hello World</h1>
    </div>
  );
};

const JulkaisuForm = ({ content, handleChange, addJulkaisu }) => {
  return (
    <div className="formContainer">
      <h1 className="formHeader">Sup m8</h1>
      <form className="formJulkaisu">
        <textarea
          maxLength="200"
          rows="5"
          cols="30"
          className="inputForm"
          type="text"
          value={content}
          onChange={handleChange}
          placeholder="Write text here"
        ></textarea>
        <div>
          <button className="julkaisuButton" onClick={addJulkaisu}>
            Lisaa
          </button>
        </div>
      </form>
    </div>
  );
};

function App() {
  const [posts, setPosts] = useState([]);
  const [content, setContent] = useState("");

  useEffect(() => {
    postService.getAll().then((initialPosts) => {
      setPosts(initialPosts);
    });
  }, []);

  const likePost = (id) => {
    const post = posts.find((n) => n.id === id);
    console.log(post);
    const likes = post.likes + 1;
    console.log(likes);
    const likedPost = {
      content: post.content,
      date: new Date().toISOString(),
      likes: likes,
    };

    postService.update(id, likedPost).then((updatedPost) => {
      postService.getAll().then((updatedPost) => {
        setPosts(updatedPost);
      });
    });
  };

  const addJulkaisu = (event) => {
    event.preventDefault();
    const julkaisuObject = {
      content: content,
      date: new Date().toISOString(),
      likes: 0,
    };
    postService.create(julkaisuObject).then((newPosts) => {
      setPosts(posts.concat(newPosts));
      setContent("");
    });
  };

  const handleChange = (event) => {
    setContent(event.target.value);
  };

  const removeJulkaisu = (id) => {
    postService.remove(id).then((newPosts) => {
      postService.getAll().then((newPosts) => {
        setPosts(newPosts);
      });
    });
  };

  return (
    <div className="main">
      <JulkaisuForm
        content={content}
        handleChange={handleChange}
        addJulkaisu={addJulkaisu}
      />
      <AllJulkaisut
        posts={posts}
        likePost={likePost}
        removeJulkaisu={removeJulkaisu}
      />
    </div>
  );
}

export default App;
